@import "../Utils/mixines";

.Parcours{
    width: 90%;
    max-width: 1200px;
    margin: auto;
    margin-top: 0px;
    display: flex;
    flex-direction: column;
    .ParcoursBox{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: flex-start;
        margin: auto;
        margin-top: 0px;
        width: 100%;
        max-width: 1200px;
        background-color: $color-5;
        border-radius: 10px;
        font-family: "montserrat";
        h2{
            width: 100%;
            text-align: center;
            margin: auto;
            font-family: 'montserrat';
            font-weight: 400;
            font-size: 30px;
            padding: 25px 0px 0px 0px;
            margin-bottom: 30px;
        }
        .card{
            width: 45%;
            height: 45%;
            margin-bottom: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            h3{
                font-size: 25px;
                font-weight: 400;
                text-align: center
            }
            p{
                font-size: 16px;
                line-height: 22px;
                text-align: center;
            }
            img{
                margin: auto;
                height: 50px;
            }
            a{
                @include mixinButtonGold;
            }
        }
    }
    .FormBox{
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
        margin: auto;
        margin-top: 30px;
        width: 100%;
        max-width: 1200px;
        background-color: $color-5;
        border-radius: 10px;
        font-family: "montserrat";
        h2{
            width: 100%;
            text-align: center;
            margin: auto;
            font-family: 'montserrat';
            font-weight: 400;
            font-size: 30px;
            padding: 25px 0px 0px 0px;
            margin-bottom: 30px;
        }
        .card{
            width: 90%;
            height: auto;
            margin-bottom: 10px;
            padding-bottom: 20px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            border-bottom: 1px solid darkgray;
            h3{
                font-size: 22px;
                font-weight: 400;
                text-align: center

            }
            p{
                font-size: 16px;
                line-height: 20px;
                margin: 5px;
                text-align: center
            }
            a{
                @include mixinButtonGold;
                margin: auto;
                margin-bottom: 0;
                margin-top: 15px;
            }
        }
    }
}

.ParcoursMini{
    background: url(../img/background.webp);
    background-size: cover;
    width: 100%;
    
    margin: auto;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;
    opacity: 0;
    font-family: "montserrat";
    animation: form 400ms ease-in forwards;
    .parcrousMiniBox{
        width: 100%;
        max-width: 1200px;
        background-color: $color-5;
        margin-top: 15px;
        margin-bottom: 15px;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        h2{
            width: 90%;
            max-width: 1200px;
            text-align: center;
            font-family: 'montserrat';
            font-size: 30px;
            font-weight: 400;
        }
        h3{
            width: 90%;
            max-width: 1200px;
            text-align: center;
            font-family: 'montserrat';
            font-size: 30px;
            font-weight: 400;
        }
        p{
            width: 95%;
            margin: auto;
            text-align: left;
            font-size: 16px;
            line-height: 25px;
            margin-bottom: 20px;
            color: $color-7;
        }
        li{
            width: 90%;
            margin-bottom: 20px;
            line-height: 25px;
            font-size: 16px;
            font-family: "montserrat";
        }
        .toParcours{
            margin-top: 20px;
            height: 50px;
            a{
                @include mixinButtonGreen;
            }
            a:hover{
                transform: scale(1.04);
            }
        }
    }
}

@keyframes form {
    0%{
        opacity: 0;;
    }
    100%{
        opacity: 1;
    }
}

