.presentation{
    margin: auto;
    margin-bottom: 0px;
    width: 100%;
    max-width: 1200px;
    margin-top: 0px;
    padding-top: 20px;
    padding-bottom: 20px;
    background: $color-10;
    border-radius: 10px;
    animation: lastReal 8000ms ease-in forwards;
    animation-iteration-count: infinite;
    .lastReal{
        width: 95%;
        margin: auto;
        background-color: $color-5;
        border-radius: 10px;
        .contentPart{
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-around;
            align-items: center;
            margin: auto;
            width: 100%;
            max-width: 1200px;
            background-color: $color-5;
            border-radius: 10px;
            .titlePart{
                width: 100%;
                h2{
                    text-align: center;
                    font-family: "montserrat";
                    font-weight: 400;
                    font-size: 30px;
                    border-radius: 10px;
                    padding: 0px;
                    color: black;
                    margin-bottom: 0px;
                }
            }
            .lastprodMini{
                padding: 0px;
                width: 26%;
                display: flex;
                a{
                    width: 100%;
                    display: flex;
                    img{
                        margin: auto;
                        width: 70%;
                        transition-duration: 300ms;
                    }
                    :hover{
                        cursor: pointer;
                    }

                }
            }
        }
    }
}


@keyframes lastReal{
    0%{
        background: $color-10;
    }
    5%{
        background: $color-11;
    }
    10%{
        background: $color-12;
    }
    15%{
        background: $color-13;
    }
    20%{
        background: $color-14;
    }
    25%{
        background: $color-15;
    }
    30%{
        background: $color-16;
    }
    35%{
        background: $color-17;
    }
    40%{
        background: $color-18;
    }
    45%{
        background: $color-19;
    }
    50%{
        background: $color-20;
    }
    55%{
        background: $color-21;
    }
    60%{
        background: $color-22;
    }
    65%{
        background: $color-23;
    }
    70%{
        background: $color-24;
    }
    75%{
        background: $color-25;
    }
    80%{
        background: $color-26;
    }
    85%{
        background: $color-27;
    }
    90%{
        background: $color-28;
    }
    95%{
        background: $color-29;
    }
    100%{
        background: $color-10;
    }
}

@keyframes lastProd {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@media only screen and (max-width: 800px){
    .presentation{
        margin-top: 10px;
        .lastReal{
            margin: auto;
            width: 95%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            font-family: "montserrat";
            .contentPart{
                padding-bottom: 20px;
                width: 100%;
                border: none;
                background-color: white;
                .titlePart{
                    margin-top: 0px;
                    width: 100%;
                    h2{
                        text-align: center;
                        font-family: "montserrat";
                        font-weight: 400;
                        font-size: 22px;
                        border-radius: 10px;
                        padding-top: 25px;
                        color: black;
                        margin-top: 0;
                        margin-bottom: 20px;
                        animation: lastProd 800ms ease-in;
                    }
                }
                .lastprodMini{
                    width: 45%;
                    margin: 0px;
                    img{
                        width: 100%;
                        padding: 0px;
                        margin: auto;
                    }
                    :hover{
                        transform: scale(1);
                        cursor: pointer;
                    }
                }
            }
        }
    }
}
